  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: var(--popout);
}
.soul-welcome-hide {
  opacity: 0;
  visibility: hidden;
}
.soul-welcome img {
  margin: 25%;
