}

.soul-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1rem;
  background-color: var(--background-color, #fff);
  -webkit-box-shadow:
  0rem
  -0.05rem
  var(--box-shadow-blur, 0.35rem)
  var(--box-shadow-color, #D6D6D6);
